<template>
	<div class="app-container">
		<p>
			<el-col :span="8">
				选择月份：
				<el-date-picker v-model="month" filters type="month" format="yyyy-MM" @change="dateChange" placeholder="选择月" :clearable="false">
				</el-date-picker>
			</el-col>
		</p>
		<el-table :data="tableData" border style="width: 100%" v-loading="isLoad">
			<el-table-column prop="orderId" label="订单号" width="150" align="center">
			</el-table-column>
			<el-table-column prop="ptId" label="流量包编号" width="80" align="center">
			</el-table-column>
			<el-table-column prop="ptName" label="流量包名称" width="150" align="center">
			</el-table-column>
			<el-table-column prop="ptPrice" label="流量包原价" width="150" align="center">
			</el-table-column>
			<el-table-column prop="discount" label="折扣" align="center">
			</el-table-column>
			<el-table-column prop="realPrice" label="流量包成本价" align="center">
			</el-table-column>
			<el-table-column prop="orderPrice" label="订单金额" width="90" align="center">
			</el-table-column>
			<el-table-column prop="grossProfit" label="毛利（含手续费）" width="90" align="center">
			</el-table-column>
		</el-table>
		<div style="text-align:center;margin: 20px auto;">
			<el-pagination style="display: inline-block" :page-size="10" @current-change="pageChange" :current-page.sync="currentPage" layout="prev, pager, next, jumper" :total="tableCount">
			</el-pagination>
		</div>
	</div>
</template>

<script>
/* eslint-disable quotes,indent,keyword-spacing,spaced-comment,no-var */
import {
	getListRechargeStatementByManager,
	getListRechargeStatementDetail
} from 'api/admin/financialSettlement';

	import { backCN } from 'utils/index';
export default {
	data() {
		return {
			tableData: [],
			month: '2017-06',
			currentMonth: '201706',
			currentPage: 1,
			tableCount: 0,
			num: 10,
			isLoad:true
		}
	},
	watch: {},
	mounted() {
	this.ListRechargeStatementDetail('201706', 1, this.num)
	},
	methods: {
		dateChange(val) {
			this.currentMonth= val.split('-').join('')
			this.ListRechargeStatementDetail(this.currentMonth, 1, this.num)
		},
		pageChange(page) {
     	this.ListRechargeStatementDetail(this.currentMonth, page, this.num)
		},
		ListRechargeStatementDetail(month,page,num){
			this.isLoad=true
              getListRechargeStatementDetail({month,page,num}).then(res=>{
				  this.isLoad=false
				  		if (res.msg == 0) {
						this.tableData = res.statementDetailList
						page === 1 ? this.tableCount = res.count : ''
					}else {
						this.tableData = []
						this.tableCount = 0
					}
					(res.msg != 0 && res.msg != 1) ? this.$message.error(backCN(res.code)) : ''
			  })
		}
	}
};
</script>

<style scoped>
.app-container {
	background: #f5f5f5;
}

.label-colum {
	font-size: 14px;
	color: #fff;
	font-weight: 100;
}
</style>


